import { Mag, MagFeedingValues } from '@/interfaces/psoMag';
import { Component, Vue } from 'vue-property-decorator';
import { allMagsList } from '../../models/PsoMagStats';

@Component
export default class PsoMagResources extends Vue {
  selectedMagName: string = 'Mag';

  get MagData(): Mag[] {
    return allMagsList;
  }

  get SelectedMag(): Mag {
    const mag = allMagsList.filter(mag => mag.name === this.selectedMagName)[0];
    return mag;
  }

  get MagFeedVals(): MagFeedingValues {
    return this.SelectedMag.feedValues;
  }

  FormatStat(stat:number): string {
    return stat > 0 ? '+'+stat : stat.toString();
  }

  GetClass(stat:number): string {
    if(stat > 9){
      return 'strong-blue';
    } else if (stat > 0) {
      return 'blue';
    } else if (stat === 0) {
      return 'grey';
    } else if(stat < -9){
      return 'strong-red';
    } else {
      return 'red';
    }
  }
  
  mounted() {
    this.$root.$emit('set-info', {
      title: "PSO Mag Resources",
      info:
        `Once again, Only accurate to GC versions.`
    });
  }
}